import {
  uniqueNamesGenerator,
  Config,
  adjectives,
  colors,
  animals,
  starWars,
  names,
  countries,
} from 'unique-names-generator';

export type Category = {
  id: string;
  name: string;
  photo?: string;
};

export type Product = {
  id: string;
  name: string;
  photo: string;
  desc?: string;
  createdAt: string;
  oldPrice?: number;
  price: number;
  category: Category;
};

//  
export const generateRandomProduct = (createdAt: string , id:number): Product => {
  const productId = id.toString() //  getRandomId(); // id 
  const category = getRandomElement(categories); // 
  const price = getRandomNumber(10000); //
  return {
    id: productId,
    name: uniqueNamesGenerator(getConfigByCategory(category.name)), //   ,   
    photo: require(`../../assets/photos/${getRandomNumber(7)}.jpg`), //  
    createdAt: createdAt,
    oldPrice: price < 4000 ? price * 1.3 : null, //   
    price: price,
    desc: uniqueNamesGenerator(getConfigByCategory('Description', ' | ')),
    category: category,
  };
};

//    ""
const categories: Category[] = [
  { id: '1', name: 'Notebook' },
  { id: '2', name: 'Phone' },
  { id: '3', name: 'Country' },
  { id: '4', name: 'Animals' },
  { id: '5', name: 'StarWars' },
];

// id
const getRandomId = (): string => {
  return Math.floor(10000000 * Math.random()).toString();
};

//      
const getRandomElement = <T>(array: T[]): T | undefined => {
  const randomIndex = getRandomNumber(array.length);
  return array[randomIndex];
};

const getRandomNumber = (max = 1000): number => {
  return Math.floor(max * Math.random());
};

//   ,     (   )
export const getConfigByCategory = (name: string, separator = ' '): Config => {
  switch (name) {
    case 'People': {
      return { separator: separator, dictionaries: [colors, names] };
    }
    case 'Animals': {
      return { separator: separator, dictionaries: [colors, animals] };
    }
    case 'Country': {
      return { separator: separator, dictionaries: [countries] };
    }
    case 'StarWars': {
      return { separator: separator, dictionaries: [starWars] };
    }
    case 'Description': {
      return { separator: separator, dictionaries: [adjectives, colors, countries] };
    }
    default: {
      return { separator: separator, dictionaries: [adjectives, colors, names] };
    }
  }
};

export type TypeProduct = {
  id: number;
  name: string;
  photos: string[];
  desc: string;
  createAt: string;
  oldPrice: number | string;
  price: number;
  category: {
    id: number;
    name: string;
    photo: string;
  };
};
